.package {
	.package__title {
	    font-size: 40px;
		font-weight: 300;
		text-transform: uppercase;
	    margin-bottom: 20px;
	}
	.package__price {
		color: #333;
		font-size: 36px;	
		font-weight: 400 !important;
	    text-align: center;
	    text-transform: uppercase;
	    margin-bottom: 17px;
	    margin-top: 38px;
	}
    .package__price__notice {
    	font-size: 13px;
    	text-align: center;
		font-weight: normal;
		text-transform: initial;
		line-height: 18px;
    }

	.package__description {
		h3 {
			font-size: 2rem;
		    text-transform: uppercase;
		    font-weight: 300;
		}

		ul {
			margin-left: 20px;
			li {
				list-style: disc;
			}
		}
	}

	.package__related {
		.package__related__title {
			font-size: 2rem;
		}
	}

	.oldPrice {
		text-decoration: line-through;
		font-size: 14px;
		color: #AEAEAE;
	}
}

.featured_package {
	.card__container {

		.svg {
			height: 500px;
		}

		h3 {
			font-size: 30px;
			font-weight: 300;
			text-transform: uppercase;
		}

		&.card__container--closed {
			.svg {
				height: 250px !important;
			}
		}

	}

}